import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {List,ListItem,Drawer,Tab,Tabs,ListItemText,ListItemIcon,MenuList,InputAdornment,TextField ,FormControlLabel,Switch,Tooltip ,GridListTileBar,GridList,GridListTile,Grid,Divider,Chip,Container,Menu,MenuItem,Fab,IconButton,Box,AppBar,Toolbar,Typography,Button,InputBase,Badge} from '@material-ui/core';


import {Toys as ToysIcon,DataUsage as DataUsageIcon,LocationCity as LocationCityIcon,Mail as MailIcon,Inbox as InboxIcon,AccountCircle as AccountCircleIcon ,Home as HomeIcon,Face as FaceIcon ,AccessAlarm as AccessAlarmIcon,Menu as MenuIcon,Search as SearchIcon, Notifications  as NotificationsIcon, Add as AddIcon,Settings as  SettingsIcon,PowerSettingsNew as  PowerSettingsNewIcon,PowerSharp as  PowerSharpIcon,Public as  PublicIcon,Favorite as FavoriteIcon} from '@material-ui/icons';

import MonthInput from './MonthInput'
import MonthUpload from './MonthUpload'
import MonthSearch from './MonthSearch'


import QuarterInput from './QuarterInput'
import QuarterUpload from './QuarterUpload'
import QuarterSearch from './QuarterSearch'

import PriceInput from './PriceInput'
import PriceUpload from './PriceUpload'
import PriceSearch from './PriceSearch'

import QuarterData from './QuarterData'
import YearData from './YearData'
import PriceData from './PriceData'
import LoadData from './LoadData'
import LvEfficiencyData from './LvEfficiencyData'
import HvEfficiencyData from './HvEfficiencyData'
import MachineryData from './MachineryData'
import Welcome from './Welcome'
import Overview from './Overview'
import EchartsFacade from './EchartsFacade'
import FormFacade from './FormFacade'



export default class Home extends React.Component
{

    constructor(props)
    {
        console.info(props);
        super(props);

        this.state={
            value:0,
            drawerOpen:false,
            //form:'Welcome',
            form:'Overview',
            drawerSaleOpen:false,
            drawerDataOpen:false,
            drawerPriceOpen:false,
        }

        this.handleChange=(e,value)=>{
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    value,
                }
            })
        }

        this.handleCloseDrawer=(event)=>{
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    drawerOpen:false,
                }
            })
        }

        this.handleOpenDrawer=(event)=>{
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    drawerOpen:true,
                }
            })
        };

        this.showComponent=(form)=>{

            //console.info(form)
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    form,
                    drawerOpen:false,
                    drawerSaleOpen:false,
                    drawerDataOpen:false,
                    drawerPriceOpen:false,
                    drawerWaterPumpOpen:false,
                }
            })

            console.info(form)
        };


        this.handleCloseDrawerWaterPump=(event)=>{
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    drawerWaterPumpOpen:false,
                }
            })
        }

        this.handleOpenDrawerWaterPump=(event)=>{
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    drawerWaterPumpOpen:true,
                }
            })
        };

        this.handleCloseDrawerSale=(event)=>{
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    drawerSaleOpen:false,
                }
            })
        }

        this.handleOpenDrawerSale=(event)=>{
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    drawerSaleOpen:true,
                }
            })
        };

        this.handleCloseDrawerPrice=(event)=>{
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    drawerPriceOpen:false,
                }
            })
        }

        this.handleOpenDrawerPrice=(event)=>{
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    drawerPriceOpen:true,
                }
            })
        };

        this.handleCloseDrawerData=(event)=>{
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    drawerDataOpen:false,
                }
            })
        }


        this.handleOpenDrawerData=(event)=>{
            this.setState((prevState,props)=>{
                return {
                    ...prevState,
                    drawerDataOpen:true,
                }
            })
        };
    }

    render() {
        return(
            <Container maxWidth={'xl'}  >
                <AppBar position="static">
                    <Toolbar >
                        <IconButton edge="start" color="inherit" aria-label="Menu" onClick={this.handleOpenDrawerWaterPump} >
                            <MenuIcon />
                        </IconButton>
                        <Typography variant="h5" style={{color:'white',fontFamily:''}} >
                            
                        </Typography>
                        {/*
                        <Button color="inherit" onClick={this.handleOpenDrawer} style={{marginLeft:100}} >
                           <SettingsIcon/>  <span style={{padding:10,fontSize:20}} className={'hwkt'}></span>
                        </Button>
                        <Button color="inherit" onClick={this.handleOpenDrawerSale} >
                            <PowerSettingsNewIcon/>  <span style={{padding:10,fontSize:20}} className={'hwkt'}></span>

                        </Button>
                        <Button color="inherit" onClick={this.handleOpenDrawerPrice} >
                            <PublicIcon/>  <span style={{padding:10,fontSize:20}} className={'hwkt'}></span>

                        </Button>
                        <Button color="inherit" onClick={this.handleOpenDrawerData} >
                            <DataUsageIcon/>  <span style={{padding:10,fontSize:20}} className={'hwkt'}></span>
                        </Button>
                        */}
                    </Toolbar>
                </AppBar>

                <Drawer anchor="left" open={this.state.drawerWaterPumpOpen} onClose={this.handleCloseDrawerWaterPump} >
                    <div style={{width:250}}>
                        <div style={{padding:20}}>
                            <AccountCircleIcon/>
                        </div>

                        <Divider/>
                        <List>
                            {[{key:11,text:'',form:'Overview'},
                                {key:12,text:'',form:'EchartsFacade'},
                                {key:13,text:'',form:'FormFacade'},
                            ].map((item, index) => (
                                <ListItem button key={item.key} onClick={()=>this.showComponent(item.form)}>
                                    <ListItemIcon>{index % 2 === 0 ? <NotificationsIcon /> : <ToysIcon />}</ListItemIcon>
                                    {/*<ListItemText primary={text} />*/}
                                    <span style={{fontSize:16,padding:10}} className="hwzs">{item.text}</span>
                                </ListItem>
                            ))}
                        </List>
                        <Divider/>
                    </div>
                </Drawer>


                <Drawer anchor="left" open={this.state.drawerOpen} onClose={this.handleCloseDrawer} >
                    <div
                        style={{width:250}}

                    >
                        <div style={{padding:20}}>
                            <AccountCircleIcon/>
                        </div>

                        <Divider/>
                        <List>
                            {[{key:1,text:'',form:'MonthInput'},
                                {key:2,text:'',form:'MonthUpload'},
                                {key:3,text:'',form:'MonthSearch'},

                            ].map((item, index) => (
                                <ListItem button key={item.key} onClick={()=>this.showComponent(item.form)}>
                                    <ListItemIcon>{index % 2 === 0 ? <NotificationsIcon /> : <ToysIcon />}</ListItemIcon>
                                    {/*<ListItemText primary={text} />*/}
                                    <span style={{fontSize:16,padding:10}} className="hwzs">{item.text}</span>
                                </ListItem>
                            ))}
                        </List>
                        <Divider/>
                        </div>
                </Drawer>

                <Drawer anchor="left" open={this.state.drawerSaleOpen} onClose={this.handleCloseDrawerSale} >
                    <div style={{width:250}}>
                        <div style={{padding:20}}>
                            <AccountCircleIcon/>
                        </div>

                        <Divider/>
                        <List>
                            {[{key:11,text:'',form:'QuarterInput'},
                                {key:12,text:'',form:'QuarterUpload'},
                                {key:13,text:'',form:'QuarterSearch'},
                            ].map((item, index) => (
                                <ListItem button key={item.key} onClick={()=>this.showComponent(item.form)}>
                                    <ListItemIcon>{index % 2 === 0 ? <NotificationsIcon /> : <ToysIcon />}</ListItemIcon>
                                    {/*<ListItemText primary={text} />*/}
                                    <span style={{fontSize:16,padding:10}} className="hwzs">{item.text}</span>
                                </ListItem>
                            ))}
                        </List>
                        <Divider/>
                    </div>
                </Drawer>

                <Drawer anchor="left" open={this.state.drawerPriceOpen} onClose={this.handleCloseDrawerPrice} >
                    <div style={{width:250}}>
                        <div style={{padding:20}}>
                            <AccountCircleIcon/>
                        </div>

                        <Divider/>
                        <List>
                            {[{key:21,text:'',form:'PriceInput'},
                                {key:22,text:'',form:'PriceUpload'},
                                {key:23,text:'',form:'PriceSearch'},
                            ].map((item, index) => (
                                <ListItem button key={item.key} onClick={()=>this.showComponent(item.form)}>
                                    <ListItemIcon>{index % 2 === 0 ? <NotificationsIcon /> : <ToysIcon />}</ListItemIcon>
                                    {/*<ListItemText primary={text} />*/}
                                    <span style={{fontSize:16,padding:10}} className="hwzs">{item.text}</span>
                                </ListItem>
                            ))}
                        </List>
                        <Divider/>
                    </div>
                </Drawer>

                <Drawer anchor="left" open={this.state.drawerDataOpen} onClose={this.handleCloseDrawerData} >
                    <div style={{width:250}}>
                        <div style={{padding:20}}>
                            <AccountCircleIcon/>
                        </div>

                        <Divider/>
                        <List>
                            {[{key:21,text:'',form:'QuarterData'},
                                {key:22,text:'',form:'YearData'},
                                {key:23,text:'',form:'LoadData'},
                                {key:24,text:'',form:'LvEfficiencyData'},
                                {key:25,text:'',form:'HvEfficiencyData'},
                                {key:26,text:'',form:'MachineryData'},
                                {key:27,text:'',form:'PriceData'},
                                ].map((item, index) => (
                                <ListItem button key={item.key} onClick={()=>this.showComponent(item.form)}>
                                    <ListItemIcon>{index % 2 === 0 ? <FavoriteIcon /> : <ToysIcon />}</ListItemIcon>
                                    {/*<ListItemText primary={text} />*/}
                                    <span style={{fontSize:18,padding:10}} className="hwzs">{item.text}</span>
                                </ListItem>
                            ))}
                        </List>
                        <Divider/>
                    </div>
                </Drawer>

                {this.state.form=='Welcome' && <Welcome/>}

                {this.state.form=='Overview' && <Overview/>}
                {this.state.form=='EchartsFacade' && <EchartsFacade/>}
                {this.state.form=='FormFacade' && <FormFacade/>}


                {this.state.form==='MonthInput' && <MonthInput/>}
                {this.state.form==='MonthUpload' && <MonthUpload/>}
                {this.state.form==='MonthSearch' && <MonthSearch/>}



                {this.state.form=='QuarterInput' && <QuarterInput/>}
                {this.state.form=='QuarterUpload' && <QuarterUpload/>}
                {this.state.form=='QuarterSearch' && <QuarterSearch/>}


                {this.state.form=='PriceInput' && <PriceInput/>}
                {this.state.form=='PriceUpload' && <PriceUpload/>}
                {this.state.form=='PriceSearch' && <PriceSearch/>}

                {this.state.form=='QuarterData' && <QuarterData/>}
                {this.state.form=='YearData' && <YearData/>}
                {this.state.form=='LoadData' && <LoadData/>}
                {this.state.form=='LvEfficiencyData' && <LvEfficiencyData/>}
                {this.state.form=='HvEfficiencyData' && <HvEfficiencyData/>}
                {this.state.form=='MachineryData' && <MachineryData/>}
                {this.state.form=='PriceData' && <PriceData/>}



            </Container>
        )

    }
}

